.App {
  text-align: center;
}

.App-logo {
  font-size: 22vmin;
  line-height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #fbf861;
  cursor: pointer;
}
